import React from 'react';
import QRCode from 'react-qr-code';

const QRCodeComponent = () => {
  const qrValue = 'https://10.0.0.168/hrmscloneproject/api/AttendanceSession/GetEmployeeAttendanceSummary'; // The value you want to encode

  return (
    <div>
      <h1>QR Code</h1>
    <QRCode
  value={qrValue}
  size={128} // Set the size of the QR code
  level="L" // Set error correction level (L, M, Q, H)
  fgColor="#000000" // Set the foreground color
  bgColor="#ffffff" // Set the background color
/>

    </div>
  );
};

export default QRCodeComponent;
